body {
  background: #999999;
  margin: auto;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.box {
    margin: auto;
    max-width: 1040px;
    border: 3px solid #000;
    background-color: #595959;
    padding: 15px;
    overflow: auto;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.mainbody {
  min-height: 100vh;
}

.maingallery {
  max-width: 80%;
  height: auto;
  width: auto
}

.flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

#subgallery img {
  max-width: 15vw;
  height: auto;
  width: auto;
  border: 3px solid #999;
  cursor: pointer;
}

#subgallery div {
  min-width: 25%;
}

#subgallery img:hover{
  border: 3px solid #000000;
}

.footer {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  margin-top: -5px;
  background-color: rgb(153, 153, 153);
  position: absolute;
  width: 50%;
  margin-left: 25%;
}

.title {
  margin-bottom: -20px;
}

h2:first-child {
  margin-bottom: 5px;
}

.tictac {
  display: flex;
  margin: auto;
  width: 20%;
  min-width: 420px;
  text-align: center;
  min-height: 380px;
  background-color: #595959;
}

.tictac div {
    margin: auto;
    text-align: center;
}

.game1 {
  margin: auto;
  width: 100px;
  padding: 10px;
}

.column {
  display: flex;
  position: relative;
  right: 75px;
  flex-direction: column;
}

.button {
  background-color: #dba212;
  display: inline-block;
  cursor: pointer;
  border: 1px solid #665528;
  width: 200px;
  margin-top: 5px;
  padding: 12px;
}
.button:hover {
  background-color: #b58f2d;
}

/*====================================================================================*/
.body {
  background-color: #323232;
  padding: 0.25rem 2rem;
}

.game{
  height:630px;width:1010px;
}

.changeOrientation{
  display:none;
}

/*@media only screen and (min-width: 600px) {
  .header {
    min-width: 1010px;
  }
}*/

@media only screen and (max-width: 600px) {

  .tictac{
    flex-flow: column;
  }

  .game{
    display:none;
  }
  .changeOrientation{
    display: block;
    background-color: #000000;
    padding-top: 20vh;
    padding-bottom: 20vh;
    color: #ffffff;
  }
 .button {
    width: 140px;
  }
  .rpg{
    width: 75vw;
  }
}
